import React, { useEffect, useContext } from 'react'
import firebase from 'firebase'
import { signinWithIntegration } from '../../../core/services/auth-service'
import Container from '../../../core/container'

export default function SignupIntegrations () {
  const { config, authUi } = useContext(Container)

  useEffect(() => {
    var uiConfig = {
      callbacks: {
        signInSuccessWithAuthResult: function () {
          // awaiting on auth state changed
        }
      },
      signInOptions: [
        firebase.auth.GoogleAuthProvider.PROVIDER_ID,
        firebase.auth.FacebookAuthProvider.PROVIDER_ID,
        firebase.auth.GithubAuthProvider.PROVIDER_ID
      ]
    }

    // The start method will wait until the DOM is loaded.
    authUi.start('#signup-integrations', uiConfig)

    firebase.auth().onAuthStateChanged(async function (user) {
      if (!user) {
        return
      }
      const idToken = await user.getIdToken(true)
      await signinWithIntegration(idToken)
    }, function (error) {
      console.error(error)
    })
  }, [authUi, config])

  return (
    <div id="signup-integrations">
    </div>
  )
}
