import React, { useState } from 'react'
import PageCover from '../page-cover'
import { signin } from '../../../core/services/auth-service'
import { useSelector } from 'react-redux'
import { Redirect, Link } from 'react-router-dom'
import LoginIntegrations from '../login-integrations'

export default function LoginPage () {
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [loading, setLoading] = useState(false)
  const [errorMessage, setErrorMessage] = useState(null)
  const auth = useSelector(state => state.auth)

  async function handleSubmit (ev) {
    ev.preventDefault()
    setLoading(true)
    setErrorMessage(null)

    try {
      await signin({
        email,
        password
      })
    } catch (e) {
      setErrorMessage(e.message)
    } finally {
      setLoading(false)
    }
  }

  if (auth) {
    return <Redirect to="/dashboard" />
  }

  return (
    <React.Fragment>
      <PageCover title="Login" />

      <section className="probootstrap-section">
        <div className="container">
          <h2 className="mb-5">Login</h2>
          <form onSubmit={handleSubmit}>
            <div className="row">
              <div className="col-md-6">
                <div className="form-group">
                  <label htmlFor="email">Email</label>
                  <input id="email" className="form-control" type="email" value={email} onChange={e => setEmail(e.target.value)} />
                </div>
                <div className="form-group">
                  <label htmlFor="password">Password</label>
                  <input id="password" className="form-control" type="password" value={password} onChange={e => setPassword(e.target.value)} />
                </div>
                <Link to="/forget-my-password">Forget my password</Link>
                {errorMessage &&
                  <div className="alert alert-danger">
                    {errorMessage}
                  </div>
                }
                <div className="form-group">
                  <input className="btn btn-primary" type="submit" value={loading ? 'Wait' : 'Login'} disabled={loading} />
                </div>
              </div>
              <div className="col-md-6">
                <LoginIntegrations />
              </div>
            </div>
          </form>
        </div>
      </section>
    </React.Fragment>
  )
}
